import React, { Component } from 'react'
import { Link } from 'gatsby'
import Img from 'gatsby-image'

import {H2, Button} from '@style/global'
import styled from 'styled-components';
import theme from '@style/theme';

class Preview extends Component {

  render() {
    const listCategory = this.props.category.map((item, i) => {
      return (
        <LsCat key={i}>
          {item}
        </LsCat>
      );
    });
    const listRole = this.props.role.map((item, i) => {
      return (
        <LsRole key={i}>
          {item}
        </LsRole>
      );
    });
    return (
      <PreviewBloc>
        
        <Info>
          <CategoryDate>{listCategory}<time dateTime={this.props.date}>{this.props.date}</time></CategoryDate>
          <H2>{this.props.title}</H2>
          <p>{this.props.desc}</p>
          <hr/>
          <p>
            <b>Roles</b><br/>
            {listRole}
          </p>
          <BtnGroup>
            {this.props.visit ? (
              <Link to={this.props.visit} target='blank'>
                <Button primary>Visit Work</Button>
              </Link>
              ) : (
              ''
            )}
            {this.props.hideStory ? (
              ''
              ) : (
              <Link to={this.props.path}>
                <Button>Read Story</Button>
              </Link>
            )}
          </BtnGroup>
        </Info>
        <ImgSlide>
              {this.props.img ? (
                <Img fluid={this.props.img} />
              ) : (
                ''
              )}
        </ImgSlide>
      </PreviewBloc>
    )
  }
}

export default Preview



const PreviewBloc = styled.div`
  display: grid;  
  height: 100% !important;
  grid-template-columns: 1fr 2fr;
  padding: 0 !important;
  margin: 0 !important;
  @media (max-width: ${theme.screen.lg}) {
    grid-template-columns: unset !important;
    height: auto !important;
  }
`;


const Info = styled.div`
  padding: 60px 40px;
  height: 100% !important;
  overflow: scroll;
  background: ${theme.color.white.regular};
  @media (max-width: ${theme.screen.lg}) {
    padding-top: 100px;
  }
`;

const ImgSlide = styled.div`
  background: ${theme.color.secondary.S900};
  div{
    height: 100% !important;
  }
  img{
    object-fit: cover;
    object-position: 50% 50%;
  }
`;

const CategoryDate = styled.p`
    color:  ${theme.color.grayscale.S600};
    margin-bottom: 20px;
    ${theme.font_size.xsmall};
`;

const LsCat = styled.span`
    ::after{
      content: ",   ";
    }
    :last-of-type{
      ::after{
        content: "ãƒ»";
      }
    }
`;

const LsRole = styled.div`
  ::before{
    content: "- ";
  }
`;

const BtnGroup = styled.div`
  margin-top: 45px;
  div{
    margin-left: 10px;
  }
  div:first-of-type{
    margin-left: 0px !important;
  }
`;